const
	{ createElement } = require("react"),
	{
		createReflexContainerForColumnElements,
		renderIntoContainerElement,
	} = require("@devsnicket/eunice-test-harnesses"),
	createYamlInputElement = require("../renderer/createYamlInputElement"),
	createYamlOutputElement = require("../renderer/createYamlOutputElement"),
	{
		getYamlFromJavaScript,
		harness: { createJavascriptInputElement },
	} = require("@devsnicket/eunice-javascript-analyzer"),
	{
		ReflexContainer,
		ReflexElement,
		ReflexSplitter,
	} = require("react-reflex");

renderIntoContainerElement({
	initialState:
		// javascriptFromWebpack is replaced with literal by webpack.config.js
		// eslint-disable-next-line no-undef
		createInitialStateFromJavascript(javascriptFromWebpack),
	renderStateful:
		stateful =>
			createReflexContainerForColumnElements(
				[
					createJavascriptInputElement({
						createStateFromValue:
							createYamlStateFromJavascript,
						stateful,
					}),
					createYamlInputElement({
						createElement,
						stateful,
					}),
					createYamlOutputElement({
						createElement,
						location,
						resizableElementTypes:
							{
								container: ReflexContainer,
								element: ReflexElement,
								splitter: ReflexSplitter,
							},
						state:
							stateful.state,
					}),
				],
			),
});

function createInitialStateFromJavascript(
	javascript,
) {
	return (
		{
			javascript,
			yaml: getYamlFromJavaScript(javascript),
		}
	);
}

function createYamlStateFromJavascript(
	javascript,
) {
	return (
		{
			yaml:
				callOrGetMessageOnError(
					() => getYamlFromJavaScript(javascript),
				),
		}
	);
}

function callOrGetMessageOnError(
	action,
) {
	try {
		return action();
	} catch (error) {
		return error.message;
	}
}